.button {
  padding: 10px 16px;
  border: 0;
  cursor: pointer;
  outline: none;
  transition: background .25s ease;
  font-size: 14px;
  &:disabled {
    cursor: auto;
  }
  &.primary {
    background: #3998F0;
    color: #ffffff;
    &:hover {
      background: #0A73F7;
    }
    &:disabled {

    }
  }

  &.success {
    background: #00CF5D;
    color: #ffffff;
    &:hover {
      background: #00AB4D;
    }
    &:disabled {
      background: #94F5A0;
    }
  }

  &.secondary {
    background: #ffffff;
    color: #47C3FA;
    &:hover {
      background: #ffffff;
      color: #38A2FA;
    }
    &:disabled {
      background: #f6f6f6;
      color: #94C5FA;
    }
  }
}
