.container {
  display: flex;
  flex-direction: column;
  overflow: hidden;
  max-height: 100vh;
  padding: 10px 20px;
}

.title {
  font-size: 24px;
  font-weight: bold;
  margin-bottom: 20px;
  color: #333333;
}

.commentInput {
  width: 100%;
}

.actions {
  display: flex;
  align-items: center;
  margin-top: 10px;
  > * {
    &:not(:first-child) {
      margin-left: 10px;
    }
  }
}

.attachedFileName {
  display: block;
  font-size: 14px;
  margin-bottom: 12px;
  font-weight: bold;
}

.sendButton {
  display: inline-flex;
  align-items: center;
  .loader {
    display: flex;
    align-items: center;
    justify-content: center;
    margin-left: 4px;
  }
}

.progressBar {
  width: 200px;
  height: 10px;
}
