.container {
  display: flex;
  flex-direction: column;
  overflow-y: auto;
  height: 100%;
}



.item {
  background: #ffffff;
  transition: background .25s ease;
  padding: 10px 8px;

  border-bottom: solid 1px #f1f1f1;

  .row {
    display: flex;
    align-items: center;
  }

  .name {
    font-size: 14px;
    color: rgba(#2B1A59, 1);
  }

  .size {
    margin-left: auto;
    margin-right: 30px;
    font-size: 12px;
    font-weight: bold;
    color: rgba(#2B1A59, 0.7);
  }

  .time {
    display: block;
    font-size: 12px;
    color: rgba(#2B1A59, 0.5);
  }

  .error {
    display: block;
    color: #FF2F36;
    font-size: 14px;
    margin-top: 15px;
  }

  .expand {
    .downloadButton {
      display: block;
      padding: 0;
      margin-top: 15px;
    }

    .comment {
      display: block;
      margin-top: 20px;
      font-size: 14px;
      color: #999999;
    }
  }
}
